button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin-right: 10px;
  padding: 10px 20px;
  font-size: 18px;
}

button:last-child {
  margin-right: 0;
}

button:hover {
  color: #fff;
  background-color: #000;
}

a {
  color: #333;
  max-width: 150px;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 320px;
  margin-bottom: 20px;
  padding: 8px 12px;
  font-size: 16px;
  font-weight: bold;
  text-decoration: none;
}

a:hover {
  color: #fff;
  background-color: #000;
}

